body {
    margin: 0;
    padding: 0;
    font-family:
        -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family:
        source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body {
    background-color: #222222;
    color: #66CCFF;
}

a {
    color: #66CCFF;
    text-decoration: none;
}

.not-implemented {
    background-color: #FFFF0033 !important;
    background-image: linear-gradient(
        -45deg,
        black 25%,
        transparent 25%,
        transparent 50%,
        black 50%,
        black 75%,
        transparent 75%
    ) !important;
    background-size: 8px 8px !important;
    border-color: #FFFF00 !important;
    color: #FFFF00 !important;
}

.app {
}

.frostmark-bookmark-list {
    min-width: 60vw;
    background-color: #333333;
}

/* TreeView vertical main line */
.tree-view {
    border-left: 1px solid #66CCFF55;
}

/* TreeView child horizontal side line */
.tree-view_children > div.info:before {
    content: "\23AF\23AF";
    font-family: monospace, monospace;
    font-size: 15px;
    color: #66CCFF55;
}
.tree-view_children > div.info {
    -webkit-transform: translate(-17px);
            transform: translate(-17px);
}

.bookmarkLabel:hover {
    color: #FFF;
    cursor: pointer;
}

.bookmarkLabelForm {
    display: flex-box;
}

.bookmarkLabelParagraph {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.bookmarkLabelInput {
    width: 50vw;

    /* make <input> and <select> the same size */
    -ms-box-sizing: border-box;
    box-sizing: border-box;
}

.sidebar {
    float: left;
    width: 25vw;
    max-width: 220px;
    height: 100vh;
    background-color: #404040;
    margin-right: 10px;
    box-shadow:         2px 0px 5px 0px rgba(0, 0, 0, 0.5);
}

.logo {
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 50%;
    text-align: center;
    border-bottom: 3px inset #333333;
}

.logo-line {
    font-size: 12px;
    white-space: pre;
}

.logo-item {
    height: 3em;
    border-left: 1px solid #66CCFF;
    border-bottom: 1px outset #333333;
}

.logo-item-label {
    padding: 1em 1em 1em 2em;
}

.logo-item:hover {
    color: #333333;
    border-left: 3px solid #66CCFF;
    cursor: pointer;

    background-color: #66CCFF;
    transition: background-color 1000ms ease-out;
}

.profile-list {
    min-width: 60vw;
    background-color: #333333;
}

.profile-list > dl {
    margin-top: 0px;
}

